.image-upload {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.picture-form {
  width: 300px;
  display: flex;
  padding: 10px;
  align-items: center;
  justify-content: space-between;
}

.picture-input {
  display: none;
}

.picture-input-label {
  cursor: pointer;
}

.upload-button {
  padding: 10px;
  border: 1px solid #ed7b2f;
  background-color: #ed7b2f;
  color: white;
  font-size: 15px;
  font-weight: bold;
  width: 100px;
  border-radius: 7px;
  cursor: pointer;
  margin: 30px 10px;
  transition: 0.3s ease-in;
}

@media screen and (max-width: 500px) {
  .image-upload {
    overflow: hidden;
  }
  .picture-form {
    width: 250px;
    padding: 5px;
  }
  .upload-button {
    margin: 5px 5px;
  }
}
